// pages/cashier.js
import { useState } from "react";
import Layout from "../components/Layout";
import toast from "react-hot-toast";
import {
  addSale,
  applySaleToInventory,
  openShift,
  closeShift,
} from "../lib/fakeBackend";

export default function Cashier() {
  const [user] = useState({ name: "ŸÖÿ≠ŸÖÿØ ÿßŸÑŸÉÿßÿ¥Ÿäÿ±", role: "cashier" });

  // ŸÖŸÜÿ™ÿ¨ÿßÿ™ ÿ™ÿ¨ÿ±Ÿäÿ®Ÿäÿ© (ÿ™ŸÇÿØÿ± ÿ™ÿ±ÿ®ÿ∑Ÿáÿß ŸÑÿßÿ≠ŸÇŸãÿß ÿ®ÿµŸÅÿ≠ÿ© ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™/ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ)
  const PRODUCTS = [
    { id: 1, name: "ÿ®ÿßŸÜÿßÿØŸàŸÑ", price: 12, barcode: "629111" },
    { id: 2, name: "ŸÅŸäÿ™ÿßŸÖŸäŸÜ ÿ≥Ÿä", price: 25, barcode: "629222" },
    { id: 3, name: "ŸÉÿ≠ŸàŸÑÿ© ÿ∑ÿ®Ÿäÿ©", price: 10, barcode: "629333" },
    { id: 4, name: "ŸÖÿ≥ŸÉŸÜ ÿ£ŸÑÿ™ÿ±ÿß", price: 18, barcode: "629444" },
  ];

  const [search, setSearch] = useState("");
  const [cart, setCart] = useState([]);
  const [discount, setDiscount] = useState(0);
  const [tax, setTax] = useState(0);
  const [paymentMethod, setPaymentMethod] = useState("cash");
  const [customer, setCustomer] = useState("ÿπŸÖŸäŸÑ ŸÜŸÇÿØŸä");

  const [invoices, setInvoices] = useState([]); // ŸÅŸàÿßÿ™Ÿäÿ± ŸàŸáŸÖŸäÿ© ŸÖÿ≠ŸÑŸäŸãÿß
  const [shiftOpen, setShiftOpen] = useState(false);

  // üîπ ŸÅŸÑÿ™ÿ±ÿ© ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™ ÿ≠ÿ≥ÿ® ÿßŸÑÿ®ÿ≠ÿ´
  const filteredProducts = PRODUCTS.filter((p) =>
    p.name.toLowerCase().includes(search.toLowerCase())
  );

  // üîπ ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨ ŸÑŸÑÿ≥ŸÑÿ©
  const addToCart = (p) => {
    if (!shiftOpen) {
      toast.error("‚ö†Ô∏è Ÿäÿ¨ÿ® ŸÅÿ™ÿ≠ ÿ¥ŸêŸÅÿ™ ŸÇÿ®ŸÑ ÿßŸÑÿ®ÿØÿ° ŸÅŸä ÿßŸÑÿ®Ÿäÿπ");
      return;
    }

    const exists = cart.find((c) => c.id === p.id);
    if (exists) {
      setCart(
        cart.map((c) =>
          c.id === p.id ? { ...c, qty: c.qty + 1 } : c
        )
      );
    } else {
      setCart([...cart, { ...p, qty: 1 }]);
    }
  };

  // üîπ ÿ•ÿ≤ÿßŸÑÿ© ŸÖŸÜ ÿßŸÑÿ≥ŸÑÿ©
  const removeItem = (id) => {
    setCart(cart.filter((c) => c.id !== id));
  };

  // üîπ ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑÿ≥ŸÑÿ©
  const subtotal = cart.reduce((sum, it) => sum + it.price * it.qty, 0);
  const total = subtotal - discount + tax;

  const formatDate = (iso) => {
    try {
      return new Date(iso).toISOString().replace("T", " ").slice(0, 16);
    } catch {
      return iso || "";
    }
  };

  // üîπ ŸÅÿ™ÿ≠ ÿ¥ŸÅÿ™
  const handleOpenShift = () => {
    openShift(user.name);
    setShiftOpen(true);
    toast.success("‚úÖ ÿ™ŸÖ ŸÅÿ™ÿ≠ ÿßŸÑÿ¥ŸêŸÅÿ™ ŸÑŸÑŸÉÿßÿ¥Ÿäÿ±");
  };

  // üîπ ÿ•ÿ∫ŸÑÿßŸÇ ÿ¥ŸÅÿ™
  const handleCloseShift = () => {
    closeShift(user.name);
    setShiftOpen(false);
    toast.success("‚úÖ ÿ™ŸÖ ÿ•ÿ∫ŸÑÿßŸÇ ÿßŸÑÿ¥ŸêŸÅÿ™");
  };

  // üîπ ÿ≠ŸÅÿ∏ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© Ÿàÿ±ÿ®ÿ∑Ÿáÿß ÿ®ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™ + ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ + ÿßŸÑÿ¥ŸêŸÅÿ™
  const saveInvoice = () => {
    if (!shiftOpen) {
      toast.error("‚ö†Ô∏è ÿßŸÅÿ™ÿ≠ ÿ¥ŸêŸÅÿ™ ÿ£ŸàŸÑÿßŸã");
      return;
    }

    if (cart.length === 0) {
      toast.error("ŸÑŸÖ Ÿäÿ™ŸÖ ÿßÿÆÿ™Ÿäÿßÿ± ÿ£Ÿä ŸÖŸÜÿ™ÿ¨");
      return;
    }

    const id = Date.now(); // ŸÉŸàÿØ ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ™ÿ¨ÿ±Ÿäÿ®Ÿä
    const date = new Date().toISOString();

    const invoice = {
      id,
      date,
      customer,
      cashier: user.name,
      payment: paymentMethod,
      type: "sale",
      items: cart.map((it) => ({
        productId: it.id,
        id: it.id,
        name: it.name,
        qty: it.qty,
        price: it.price,
        barcode: it.barcode,
      })),
      discount,
      tax,
      total,
    };

    // 1) ÿ≠ŸÅÿ∏ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© ŸÅŸä "ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™"
    const saved = addSale(invoice);

    // 2) ÿÆÿµŸÖ ÿßŸÑŸÉŸÖŸäÿßÿ™ ŸÖŸÜ ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ
    applySaleToInventory(saved);

    // 3) ÿ•ÿ∂ÿßŸÅÿ© ŸÑŸÑÿ≥ÿ¨ŸÑ ÿßŸÑŸÖÿ≠ŸÑŸä ŸÅŸä ÿ¥ÿßÿ¥ÿ© ÿßŸÑŸÉÿßÿ¥Ÿäÿ±
    setInvoices((prev) => [...prev, saved]);

    // 4) ÿ™ÿµŸÅŸäÿ± ÿßŸÑÿ≥ŸÑÿ©
    setCart([]);
    setDiscount(0);
    setTax(0);

    toast.success("üßæ ÿ™ŸÖ ÿ≠ŸÅÿ∏ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© Ÿàÿ™ÿ≠ÿØŸäÿ´ ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™ ŸàÿßŸÑŸÖÿÆÿ≤ŸàŸÜ (ŸÖÿ≠ŸÑŸäŸãÿß)");
  };

  // üîπ ÿ∑ÿ®ÿßÿπÿ© ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©
  const printInvoice = (inv) => {
    const html = `
      <html dir="rtl" lang="ar">
      <head>
        <meta charset="utf-8" />
        <title>ŸÅÿßÿ™Ÿàÿ±ÿ© ${inv.id}</title>
        <style>
          body { font-family: 'Tajawal',sans-serif; padding: 20px; }
          h2 { color:#0ea5e9; margin-bottom: 10px; }
          table { width:100%; border-collapse: collapse; margin-top:10px; }
          th,td { border:1px solid #ddd; padding:6px; text-align:center; }
          th { background:#f3f4f6; }
        </style>
      </head>
      <body>
        <h2>ÿµŸäÿØŸÑŸäÿ© ÿßŸÑŸÖÿπŸÑŸÖ</h2>
        <p>ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ±ŸÇŸÖ: <strong>${inv.id}</strong></p>
        <p>ÿßŸÑÿπŸÖŸäŸÑ: ${inv.customer}</p>
        <p>ÿßŸÑŸÉÿßÿ¥Ÿäÿ±: ${inv.cashier}</p>
        <p>ÿßŸÑÿ™ÿßÿ±ŸäÿÆ: ${formatDate(inv.date)}</p>

        <table>
          <thead>
            <tr>
              <th>ÿßŸÑÿµŸÜŸÅ</th>
              <th>ÿßŸÑŸÉŸÖŸäÿ©</th>
              <th>ÿßŸÑÿ≥ÿπÿ±</th>
              <th>ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä</th>
            </tr>
          </thead>
          <tbody>
            ${
              (inv.items || [])
                .map(
                  (it) => `
                  <tr>
                    <td>${it.name}</td>
                    <td>${it.qty}</td>
                    <td>${it.price}</td>
                    <td>${it.qty * it.price}</td>
                  </tr>`
                )
                .join("") || `
                <tr>
                  <td colspan="4">ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿ£ÿµŸÜÿßŸÅ</td>
                </tr>`
            }
          </tbody>
        </table>

        <h3>ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÜŸáÿßÿ¶Ÿä: ${inv.total} ÿ±.ÿ≥</h3>

        <script>
          window.onload = () => {
            window.print();
            setTimeout(() => window.close(), 800);
          };
        </script>
      </body>
      </html>
    `;

    const w = window.open("", "_blank", "width=900,height=900");
    w.document.write(html);
    w.document.close();
  };

  return (
    <Layout user={user} title="ŸÜÿ∏ÿßŸÖ ÿßŸÑŸÉÿßÿ¥Ÿäÿ±">
      <div dir="rtl" className="space-y-6">

        {/* ÿ¥ÿ±Ÿäÿ∑ ÿßŸÑÿ¥ŸêŸÅÿ™ */}
        <div className="flex flex-col gap-3 p-4 bg-white border rounded-lg shadow-sm md:flex-row md:items-center md:justify-between">
          <div>
            <p className="text-sm text-gray-600">
              ÿßŸÑŸÉÿßÿ¥Ÿäÿ± ÿßŸÑÿ≠ÿßŸÑŸä: <span className="font-semibold">{user.name}</span>
            </p>
            <p className="text-xs text-gray-500">
              ÿ≠ÿßŸÑÿ© ÿßŸÑÿ¥ŸêŸÅÿ™:{" "}
              <span className={shiftOpen ? "text-emerald-600" : "text-red-600"}>
                {shiftOpen ? "ŸÖŸÅÿ™Ÿàÿ≠" : "ŸÖÿ∫ŸÑŸÇ"}
              </span>
            </p>
          </div>
          <div className="flex flex-wrap gap-2">
            <button
              onClick={handleOpenShift}
              className="px-3 py-1.5 text-xs text-white bg-emerald-600 rounded-md hover:bg-emerald-700"
            >
              üü¢ ŸÅÿ™ÿ≠ ÿ¥ŸêŸÅÿ™
            </button>
            <button
              onClick={handleCloseShift}
              className="px-3 py-1.5 text-xs text-white bg-red-500 rounded-md hover:bg-red-600"
            >
              üî¥ ÿ•ÿ∫ŸÑÿßŸÇ ÿ¥ŸêŸÅÿ™
            </button>
          </div>
        </div>

        {/* ÿßŸÑÿ¥ÿ®ŸÉÿ© ÿßŸÑÿ±ÿ¶Ÿäÿ≥Ÿäÿ©: ÿßŸÑÿ≥ŸÑÿ© + ÿßŸÑÿ®ÿ≠ÿ´ ÿπŸÜ ŸÖŸÜÿ™ÿ¨ */}
        <div className="grid grid-cols-1 gap-6 md:grid-cols-3">
          {/* ÿßŸÑÿ≥ŸÑÿ© */}
          <div className="p-5 bg-white border rounded-lg shadow-sm md:col-span-2">
            <h2 className="mb-4 text-lg font-bold">üßæ ÿßŸÑÿ≥ŸÑÿ©</h2>

            {/* ÿ®ŸäÿßŸÜÿßÿ™ ÿßŸÑÿπŸÖŸäŸÑ Ÿàÿ∑ÿ±ŸäŸÇÿ© ÿßŸÑÿØŸÅÿπ */}
            <div className="grid grid-cols-1 gap-3 mb-4 text-sm md:grid-cols-3">
              <input
                type="text"
                value={customer}
                onChange={(e) => setCustomer(e.target.value)}
                className="w-full px-3 py-2 border rounded-md"
                placeholder="ÿßÿ≥ŸÖ ÿßŸÑÿπŸÖŸäŸÑ (ÿßÿÆÿ™Ÿäÿßÿ±Ÿä)"
              />
              <select
                value={paymentMethod}
                onChange={(e) => setPaymentMethod(e.target.value)}
                className="w-full px-3 py-2 border rounded-md"
              >
                <option value="cash">ŸÜŸÇÿØŸãÿß</option>
                <option value="card">ÿ®ÿ∑ÿßŸÇÿ©</option>
                <option value="wallet">ŸÖÿ≠ŸÅÿ∏ÿ©</option>
              </select>
              <div className="text-xs text-gray-500 md:text-right">
                ŸÜŸàÿπ ÿßŸÑÿØŸÅÿπ Ÿäÿ§ÿ´ÿ± ÿπŸÑŸâ ÿßŸÑÿ™ŸÇÿßÿ±Ÿäÿ± ŸÑÿßÿ≠ŸÇŸãÿß ŸÅŸÇÿ∑ (ÿ®ŸäÿßŸÜÿßÿ™ ÿ™ÿ¨ÿ±Ÿäÿ®Ÿäÿ©).
              </div>
            </div>

            {cart.length === 0 ? (
              <p className="text-sm text-gray-500">
                ŸÑÿß ŸäŸàÿ¨ÿØ ŸÖŸÜÿ™ÿ¨ÿßÿ™ ŸÖÿ∂ÿßŸÅÿ© ÿ®ÿπÿØ. ÿßÿÆÿ™ÿ± ŸÖŸÜÿ™ÿ¨Ÿãÿß ŸÖŸÜ ÿßŸÑŸÇÿßÿ¶ŸÖÿ© ÿπŸÑŸâ ÿßŸÑŸäŸÖŸäŸÜ.
              </p>
            ) : (
              <table className="w-full text-sm">
                <thead>
                  <tr className="text-gray-600 bg-gray-50">
                    <th className="p-2">ÿßŸÑÿµŸÜŸÅ</th>
                    <th>ÿßŸÑŸÉŸÖŸäÿ©</th>
                    <th>ÿßŸÑÿ≥ÿπÿ±</th>
                    <th>ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä</th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  {cart.map((it) => (
                    <tr key={it.id} className="border-t">
                      <td className="p-2">{it.name}</td>
                      <td>{it.qty}</td>
                      <td>{it.price} ÿ±.ÿ≥</td>
                      <td>{it.qty * it.price} ÿ±.ÿ≥</td>
                      <td>
                        <button
                          className="text-xs text-red-500"
                          onClick={() => removeItem(it.id)}
                        >
                          ÿ≠ÿ∞ŸÅ
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            )}

            {/* ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸäÿßÿ™ */}
            <div className="mt-4 space-y-2 text-sm">
              <p>
                ÿßŸÑŸÖÿ¨ŸÖŸàÿπ: <strong>{subtotal} ÿ±.ÿ≥</strong>
              </p>

              <label>ÿÆÿµŸÖ</label>
              <input
                type="number"
                className="w-full p-1 border rounded"
                value={discount}
                onChange={(e) => setDiscount(Number(e.target.value) || 0)}
              />

              <label>ÿ∂ÿ±Ÿäÿ®ÿ©</label>
              <input
                type="number"
                className="w-full p-1 border rounded"
                value={tax}
                onChange={(e) => setTax(Number(e.target.value) || 0)}
              />

              <p className="mt-2 text-lg font-bold">
                ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÜŸáÿßÿ¶Ÿä:{" "}
                <span className="text-emerald-600">{total} ÿ±.ÿ≥</span>
              </p>

              <button
                className="w-full py-2 mt-3 text-white rounded bg-emerald-600 hover:bg-emerald-700"
                onClick={saveInvoice}
              >
                üíæ ÿ≠ŸÅÿ∏ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© Ÿàÿ™ÿ≠ÿØŸäÿ´ ÿßŸÑŸÜÿ∏ÿßŸÖ
              </button>
            </div>
          </div>

          {/* ÿßŸÑÿ®ÿ≠ÿ´ ÿπŸÜ ŸÖŸÜÿ™ÿ¨ */}
          <div className="p-5 bg-white border rounded-lg shadow-sm">
            <h2 className="text-lg font-bold">üîç ÿßŸÑÿ®ÿ≠ÿ´ ÿπŸÜ ŸÖŸÜÿ™ÿ¨</h2>
            <input
              type="text"
              placeholder="ÿßÿ≥ŸÖ ÿßŸÑŸÖŸÜÿ™ÿ¨..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className="w-full px-3 py-2 mt-2 border rounded-md"
            />

            <div className="mt-4 space-y-2 max-h-[300px] overflow-y-auto">
              {filteredProducts.map((p) => (
                <button
                  key={p.id}
                  className="w-full p-2 text-right border rounded hover:bg-gray-50"
                  onClick={() => addToCart(p)}
                >
                  {p.name} ‚Äî {p.price} ÿ±.ÿ≥
                </button>
              ))}
              {filteredProducts.length === 0 && (
                <p className="text-xs text-gray-400">
                  ŸÑÿß ÿ™Ÿàÿ¨ÿØ ŸÜÿ™ÿßÿ¶ÿ¨ ŸÖÿ∑ÿßÿ®ŸÇÿ© ŸÑÿ®ÿ≠ÿ´ŸÉ.
                </p>
              )}
            </div>
          </div>
        </div>

        {/* ÿ≥ÿ¨ŸÑ ÿßŸÑŸÅŸàÿßÿ™Ÿäÿ± ÿßŸÑÿ£ÿÆŸäÿ±ÿ© ŸÑŸÑŸÉÿßÿ¥Ÿäÿ± */}
        <div className="p-5 bg-white border rounded-lg shadow-sm">
          <h2 className="mb-4 text-lg font-bold">üïí ÿ¢ÿÆÿ± ÿßŸÑŸÅŸàÿßÿ™Ÿäÿ± (ŸÖÿ≠ŸÑŸäŸãÿß)</h2>

          {invoices.length === 0 ? (
            <p className="text-sm text-gray-500">ŸÑÿß ÿ™Ÿàÿ¨ÿØ ŸÅŸàÿßÿ™Ÿäÿ± ÿ®ÿπÿØ.</p>
          ) : (
            <div className="overflow-x-auto">
              <table className="w-full text-sm min-w-[600px]">
                <thead>
                  <tr className="text-gray-600 bg-gray-50">
                    <th className="p-2">ÿ±ŸÇŸÖ</th>
                    <th>ÿßŸÑÿ™ÿßÿ±ŸäÿÆ</th>
                    <th>ÿßŸÑÿπŸÖŸäŸÑ</th>
                    <th>ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä</th>
                    <th>ÿ•ÿ¨ÿ±ÿßÿ°ÿßÿ™</th>
                  </tr>
                </thead>
                <tbody>
                  {invoices.map((inv) => (
                    <tr key={inv.id} className="border-t">
                      <td className="p-2">{inv.id}</td>
                      <td>{formatDate(inv.date)}</td>
                      <td>{inv.customer}</td>
                      <td>{inv.total} ÿ±.ÿ≥</td>
                      <td>
                        <button
                          className="text-xs text-sky-600"
                          onClick={() => printInvoice(inv)}
                        >
                          ÿ∑ÿ®ÿßÿπÿ©
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>
    </Layout>
  );
}












